.home {
    text-align: center;
    animation: open 1s;
    min-height: 100vh;
    .title {
        color: var(--yellow);
        text-align: center;
        padding: 25px 0;
    }
    .name {
        text-align: center;
        color: var(--white);
        font-weight: 600;
        font-size: 60px;
        span {
            color: var(--yellow);
            width: 225px;
            opacity: 0.8;
            animation: appear 0.1s infinite ease-in-out;
            display: inline-block;
            font-weight: 700;
        }
    }
    p {
        font-weight: 300;
        color: var(--gray);
        text-align: center;
        max-width: 600px;
        margin: 0 auto;
        font-size: 21px;
        margin-top: 70px;
    }
    .work {
        display: inline-flex;
        align-items: stretch;
        justify-content: space-evenly;
        margin: 0 auto;
        margin-top: 50px;
        padding: 25px 30px;
        border-top: 1px solid var(--light-gray);
        border-bottom: 1px solid var(--light-gray);
        .vert {
            width: 1px;
            margin: 0px 25px;
            background-color: var(--light-gray);
        }
        &__text {
            display: flex;
            align-items: center;
            justify-content: center;
            span {
                font-size: 19px;
                line-height: 18px;
                font-weight: 300;
                color: var(--white);
                text-align: center;
            }
            b {
                display: block;
                height: 12px;
                width: 12px;
                border-radius: 50%;
                background-color: var(--yellow);
                margin-right: 10px;
                animation: migat 1s infinite ease;
            }
        }
        &__links {
            display: flex;
            align-items: center;
            a {
                display: inline-block;
                height: 25px;
                width: 25px;
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
            svg {
                width: 25px;
                height: 25px;

                transition: 0.1s;
                * {
                    transition: 0.1s;
                }

                &:hover {
                    * {
                        fill: var(--white);
                        transition: 0.1s;
                    }
                }
            }
        }
    }
}

@keyframes appear {
    0% {
        opacity: 1;
        transform: translateX(-0.5px);
    }
    50% {
        opacity: 0.7;
        transform: translateX(0px);
    }
    100% {
        opacity: 1;
        transform: translateX(-0.5px);
    }
}
@keyframes migat {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes open {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
